/* ==========================================================================
    NAVIGATION
   ========================================================================== */

/* Active Link */
[data-template="home"] .navbar .nav-item__home a,
.navbar [data-active-item="active"] a {
  color: var(--bs-navbar-active-color);
}


/* MOBILE BUTTON
  ========================================================================== */
// Reset
.navbar-toggler,
.btn-close {
  border: 0;
  border-radius: 0;
  opacity: 1;
  outline: 0;
  padding: 5px;
  transition: opacity 200ms ease;
}

// Remove BS icon
.navbar-toggler-icon,
.btn-close {
  background: none;
}

// BTN size
.navbar-toggler-icon,
.btn-close {
  width: 22px;
  height: 22px;
}

// Icon
.navbar-toggler-icon svg,
.btn-close-icon svg {
  width: 100%;
  height: 100%;
  vertical-align: top;
}

// Close BTN style
.btn-close-icon {
  background: none;
  display: block;
  padding: 3px;
}

// Hover style
.navbar-toggler:hover,
.btn-close:hover {
  opacity: 1;
}

// Focus style
.navbar-toggler:focus,
.btn-close:focus {
  box-shadow: none;
  opacity: 1;
}
.navbar-toggler:focus-visible,
.btn-close:focus-visible {
  box-shadow: 0 0 0 3px #0d6efd;
  opacity: 1;
}


/* Close button */
.btn-close::after {
